<template>
  <div class="home-page">
    <h1>{{ title }}</h1>
    <el-link @click="loginOut" type="success">Login</el-link>

    <el-card class="box-card" shadow="hover">
      <template #header>
        <div class="card-header">
          <span>User Info</span>
        </div>
      </template>
      <div class="text item">
        <div class="info-item">
          <span>Name:</span>
          <span>{{ userInfo.name }}</span>
        </div>
      </div>
      <div class="text item">
        <div class="info-item">
          <span>Certify Code:</span>
          <span>{{ userInfo.certify_code }}</span>
        </div>
        <el-button @click="updateCertifyCode">{{
          userInfo.certify_code === ""
            ? "Generate Certify Code"
            : "Update Certify Code"
        }}</el-button>
      </div>
    </el-card>
    <el-card class="box-card" shadow="hover">
      <template #header>
        <div class="card-header">
          <span>Dorm Status</span>
          <el-button @click="fetchBuildingsStatus" class="button" type="primary"
            >Refresh</el-button
          >
        </div>
      </template>
      <div v-for="status in buildingsStatus" :key="status" class="text item">
        {{ status.building_name }}: Female {{ status.female_available }}/{{
          status.female_total
        }}. Male {{ status.male_available }}/{{ status.male_total }}
      </div>
    </el-card>
    <div v-if="userDormInfo.roomId === undefined">
      <div class="no-dorm-tips">
        <p>未查到您的宿舍信息，请选择宿舍 或 刷新信息</p>
        <el-button @click="getUser2RoomInfo">Refresh</el-button>
      </div>
      <div class="selection-container box-card">
        <el-form
          ref="selectionForm"
          :model="selectionForm"
          :rules="rules"
          @keyup.enter="submitForm"
        >
          <el-form-item label="Building" :prop="buildingId">
            <el-select
              v-model="selectionForm.buildingId"
              placeholder="Select Building"
            >
              <el-option
                v-for="item in buildingsStatus"
                :key="item.building_id"
                :label="item.building_name"
                :value="item.building_id"
              >
                <span>{{ item.building_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="Nums" :prop="usersNum">
            <el-input-number
              v-model="selectionForm.usersNum"
              :min="1"
              :max="4"
              @change="handleNumChange"
            />
          </el-form-item>

          <div v-for="(user, index) in selectionForm.users" :key="user.userId">
            <el-form-item
              :label="'Student ' + index + ' ID:'"
              :prop="user.userStudentId"
            >
              <el-input
                :disabled="index === 0"
                v-model="user.userStudentId"
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="'Student ' + index + ' Certify Code:'"
              :prop="user.userCertifyCode"
            >
              <el-input
                :disabled="index === 0"
                v-model="user.userCertifyCode"
              ></el-input>
            </el-form-item>
          </div>

          <el-form-item>
            <el-button type="primary" style="width: 46%" @click="toRegister"
              >前往注册</el-button
            >
            <el-button
              type="primary"
              style="width: 46%; margin-left: 8%"
              @click="submitForm"
              >提 交</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div v-else>你的宿舍是{{ userDormInfo.roomId }}</div>
  </div>
</template>

<script>
import { useRouter } from "vue-router";
import { store } from "@/store";
import { updateCertifyCode } from "@/api/user";
import { buildingsStatus, user2RoomInfo, postBookOrder } from "@/api/dorm";
import { mapActions } from "vuex";
import { ref } from "vue";

export default {
  data: function () {
    return {
      title: "Home",
      userInfo: {
        id: 1,
        name: "John",
        certify_code: "12345678",
      },
      buildingsStatus: [
        {
          id: 1,
          building_name: "Building 1",
          female_available: 1,
          female_total: 10,
          male_available: 10,
          male_total: 20,
        },
      ],
      userDormInfo: {},
      selectionForm: {
        buildingId: "",
        users: [
          {
            userId: ref(""),
            userStudentId: ref(""),
            userCertifyCode: ref(""),
          },
        ],
        usersNum: 1,
      },
      rules: {},
    };
  },
  methods: {
    ...mapActions("user", ["LoginOut"]),
    async loginOut() {
      return await this.LoginOut();
    },
    updateCertifyCode: function () {
      const that = this;
      updateCertifyCode().then((res) => {
        that.userInfo.certify_code = res.data.certifyCode;
      });
    },

    fetchBuildingsStatus: function () {
      const that = this;
      buildingsStatus().then((res) => {
        that.buildingsStatus = res.data;
      });
    },

    getUser2RoomInfo: function () {
      const that = this;
      user2RoomInfo().then((res) => {
        console.log(res);
        that.userDormInfo = res.data.userDormInfo;
      });
    },

    handleNumChange: function (val) {
      // this.selectionForm.users = [];
      let diff = val - this.selectionForm.users.length;
      console.log(diff);
      if (diff > 0) {
        for (let i = 0; i < diff; i++) {
          this.selectionForm.users.push({
            userId: ref(""),
            userStudentId: ref(""),
            userCertifyCode: ref(""),
          });
        }
      } else {
        for (let i = 0; i < -diff; i++) {
          this.selectionForm.users.pop();
        }
      }
    },

    submitForm: function () {
      this.$refs.selectionForm.validate(async (valid) => {
        if (valid) {
          await this.bookOrder();
          if (this.userDormInfo.roomId === undefined) {
            this.$message.error("请选择宿舍");
          } else {
            this.$message.success("提交成功");
          }
        }
      });
    },

    bookOrder: function () {
      const that = this;
      const { buildingId, users, usersNum } = this.selectionForm;
      const payload = {
        buildingId,
        users,
        usersNum,
        gender: that.userInfo.gender,
      };
      postBookOrder(payload).then((res) => {
        console.log(res);
      });
    },
  },
  setup() {
    // const route = useRoute();
    const router = useRouter();
    const toLogin = () => {
      router.push("/login");
    };
    return {
      toLogin: toLogin,
    };
  },
  mounted() {
    // store.dispatch("getUserInfo");
    this.userInfo = store.getters["user/userInfo"];
    this.fetchBuildingsStatus();
    this.getUser2RoomInfo();
    this.selectionForm.users[0].userId = this.userInfo.id;
    this.selectionForm.users[0].userStudentId = this.userInfo.student_id;
    this.selectionForm.users[0].userCertifyCode = this.userInfo.certify_code;
  },
};
</script>
<style  scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.box-card {
  width: 480px;
}

.home-page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  overflow: scroll !important;
}
</style>
