import service from "@/utils/request";

// @Summary 获取楼列表
// @Produce  application/json
// @Router /buildings [get]
export const buidings = () => {
  return service({
    url: "/buildings",
    method: "get",
  })
}

export const buildingsStatus = () => {
  return service({
    url: "/buildingsStatus",
    method: "get",
  })
}

export const user2RoomInfo = () => {
  return service({
    url: "/user2RoomInfo",
    method: "get",
  })
}

export const postBookOrder = (data) => {
  return service({
    url: "/bookOrder",
    method: "post",
    data: data,
  })
}
